.App {
  display: flex;
  font-family: roboto;
}

.sidebar {
  box-sizing: border-box;
  width: 20%;
  border-right: 1px solid black;
  height: 100vh;
  position: fixed;
  padding: 10px;
}

.main-content {
  text-align: center;
  position: relative;
  left: 20%;
  width: 80%;
}

.all-panels {
  height: 70vh;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.panel {
  display: flex;
  padding: 5px 15px;
  margin-top: 15px;
  align-items: center;
  border-radius: 5px;
}

.panel h3 {
  margin-left: 30px;
}

.panel img {
  width: 30px;
  height: 30px;
}

.panel:hover {
  background-color: rgb(200, 196, 196);
  cursor: pointer;
}

.profile-pic {
  margin: 50px;
}

.follow {
  display: flex;
  justify-content: center;
}

.follow h3 {
  margin: 15px;
}

.picture-holder {
  width: 60%;
  margin: auto;
}

.picture img {
  width: 100%;
  border-radius: 5px;
}
